<script>
import Layout from "@/layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";

import { userGridData } from "./data-user";

/**
 * Contacts-list component
 */
export default {
  page: {
    title: "Contact User List",
    meta: [{ name: "description", content: appConfig.description }]
  },
  components: { Layout, PageHeader },
  data() {
    return {
      userGridData: userGridData,
      title: "공지사항",
      items: [
        {
          text: "종합지표현황",
          href: "/"
        },
        {
          text: "공지사항",
          active: true
        }
      ]
    };
  }
};

</script>

<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-lg-12">
        <div class="card">
          <div class="card-body">
            <div class="table-responsive">
              <table class="table table-centered table-nowrap table-hover">
                <thead class="thead-light">
                  <tr>
                    <th scope="col" style="width: 10px;">#</th>
                    <th scope="col" style="width: 60%;">제목</th>
                    <th scope="col" style="width: 70px;">작성자</th>
                    <th scope="col" style="width: 50px;">작성일</th>
                    <th scope="col" style="width: 20px;">첨부파일</th>
                    <th scope="col" style="width: 20px;">조회수</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="list in userGridData" :key="list.id">
                    <td>
                      {{list.id}}
                    </td>
                    <td>
                      <h5 class="font-size-14 mb-1">
                        <router-link tag="a" to="/noticeboard/register" class="text-dark">
                          {{list.title}}
                        </router-link>
                        
                      </h5>
                    </td>
                    <td>
                        <a href="#" class="text-dark">{{list.writer}}</a>
                    </td>
                    <td>
                        {{list.updateDate}}
                    </td>
                    <td>
                      <div>
                        {{list.addfilecnt}}
                      </div>
                    </td>
                    <td>{{ list. readcnt}}</td>
                    
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="row">
              <div class="col-lg-12">
                <ul class="pagination pagination-rounded justify-content-center mt-4">
                  <li class="page-item disabled">
                    <a href="#" class="page-link">
                      <i class="mdi mdi-chevron-left"></i>
                    </a>
                  </li>
                  <li class="page-item">
                    <a href="#" class="page-link">1</a>
                  </li>
                  <li class="page-item active">
                    <a href="#" class="page-link">2</a>
                  </li>
                  <li class="page-item">
                    <a href="#" class="page-link">3</a>
                  </li>
                  <li class="page-item">
                    <a href="#" class="page-link">4</a>
                  </li>
                  <li class="page-item">
                    <a href="#" class="page-link">5</a>
                  </li>
                  <li class="page-item">
                    <a href="#" class="page-link">
                      <i class="mdi mdi-chevron-right"></i>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>